/*Global Settings*/
body {
  font-family: 'Raleway', sans-serif;
  color: #1B1F23;
}

/*Text-colors*/
.text-main-color {
  color: #2FCCF6;
}

.text-muted {
  color: #BEC1C5 !important;
}

/*Font-size*/
h1, .h1, h2, .h2 {
  font-weight: 800;
}

h3, .h3, h4, .h4, button, .btn {
  font-weight: 700;
}

p, .p {
  font-weight: 500;
}

div, .div, span, .span {
  font-weight: 400;
}

h1, .h1 {
  font-size: 3rem;
}

@media (max-width: 1199.98px) {
  h1, .h1 {
    font-size: 2.49rem;
  }
}

@media (max-width: 991.98px) {
  h1, .h1 {
    font-size: 1.92rem;
  }
}

@media (max-width: 767.98px) {
  h1, .h1 {
    font-size: 1.44rem;
  }
}

@media (max-width: 575.98px) {
  h1, .h1 {
    font-size: 1.05rem;
  }
}

h2, .h2 {
  font-size: 2.5rem;
}

@media (max-width: 1199.98px) {
  h2, .h2 {
    font-size: 2.075rem;
  }
}

@media (max-width: 991.98px) {
  h2, .h2 {
    font-size: 1.6rem;
  }
}

@media (max-width: 767.98px) {
  h2, .h2 {
    font-size: 1.2rem;
  }
}

h3, .h3 {
  font-size: 1.5rem;
}

@media (max-width: 1199.98px) {
  h3, .h3 {
    font-size: 1.245rem;
  }
}

h4, .h4 {
  font-size: 1.125rem;
}

@media (max-width: 767.98px) {
  h4, .h4 {
    font-size: 0.93375rem;
  }
}

p, .p {
  font-size: 1.5rem;
  letter-spacing: 0.86px;
  line-height: 1.1;
}

@media (max-width: 1199.98px) {
  p, .p {
    font-size: 1.245rem;
  }
}

@media (max-width: 767.98px) {
  p, .p {
    font-size: 0.96rem;
  }
}

div, .div {
  font-size: 1rem;
  line-height: 1.2;
}

@media (max-width: 767.98px) {
  div, .div {
    font-size: 0.83rem;
  }
}

span, .span {
  font-size: 0.875rem;
}

button, .btn {
  font-size: 1rem;
}

.btn {
  padding: 0.7rem 2rem;
  border-radius: 2rem;
}

.btn-outline-primary {
  color: #2FCCF6;
  border-color: #2FCCF6;
}

.btn-outline-primary:hover, .btn-outline-primary:not(:disabled):not(.disabled).active {
  color: #fff;
  background-color: #2FCCF6;
  border-color: #2FCCF6;
}

.btn-sm {
  padding: 0.4rem 1.5rem;
  border-radius: 1.5rem;
}

/* Start Navbar */
.navbar {
  padding: 2.25em 1em 0;
}

@media (max-width: 767.98px) {
  .navbar {
    padding: 1em;
  }
}

.navbar .navbar-brand {
  margin-right: 2em;
}

.navbar .nav-item .nav-link {
  color: #1B1F23;
}

.navbar .nav-item .nav-link:hover, .navbar .nav-item .nav-link:active, .navbar .nav-item .nav-link:focus {
  color: #2FCCF6;
}

@media (max-width: 767.98px) {
  .navbar .nav-item .nav-link {
    padding-top: 1em;
    padding-bottom: 0;
  }
}

.navbar .social .fa {
  background-color: #000000;
  color: #ffffff;
  padding: 0.25em;
}

.navbar .social .fa:hover {
  color: #2FCCF6;
}

@media (max-width: 767.98px) {
  .navbar .social {
    margin-top: 20px;
  }
}

/* End Navbar */
/*Start Home Slider*/
.home-slider {
  position: relative;
  overflow: hidden;
}

@media (max-width: 991.98px) {
  .home-slider {
    align-items: start !important;
  }
}

.home-slider > div:first-child {
  position: absolute;
}

@media (max-width: 575.98px) {
  .home-slider > div:first-child {
    position: initial;
  }
}

.home-slider .carousel .carousel-indicators {
  bottom: -7em;
  right: initial;
  justify-content: start;
  padding-left: 0;
  margin-right: initial;
  margin-left: 0;
}

@media (max-width: 1199.98px) {
  .home-slider .carousel .carousel-indicators {
    bottom: -5em;
  }
}

@media (max-width: 767.98px) {
  .home-slider .carousel .carousel-indicators {
    bottom: -3em;
  }
}

@media (max-width: 575.98px) {
  .home-slider .carousel .carousel-indicators {
    bottom: 0;
    right: 0;
    justify-content: center;
    margin-right: auto;
    margin-left: auto;
  }
}

.home-slider .carousel .carousel-indicators li {
  border: 1px solid transparent;
  background-color: #E4ECEE;
  opacity: 1;
  width: 1em;
  height: 1em;
  border-radius: 50%;
}

.home-slider .carousel .carousel-indicators li.active, .home-slider .carousel .carousel-indicators li:hover, .home-slider .carousel .carousel-indicators li:focus {
  background-color: #2FCCF6;
  border-color: #979797;
}

.home-slider .carousel .carousel-item .carousel-caption {
  position: static;
  color: #1B1F23;
}

@media (max-width: 991.98px) {
  .home-slider .carousel .carousel-item .carousel-caption {
    padding-top: 50px;
  }
}

@media (max-width: 575.98px) {
  .home-slider .carousel .carousel-item .carousel-caption {
    padding-top: 20px;
    padding-bottom: 50px;
  }
}

.home-slider .carousel .carousel-item .carousel-caption .h1 {
  margin-bottom: 38px;
}

@media (max-width: 767.98px) {
  .home-slider .carousel .carousel-item .carousel-caption .h1 {
    margin-bottom: 20px;
  }
}

/*End Home Slider*/
/*Start Sponser*/
.sponsers {
  background-color: #F3F6F6;
}

.sponsers img {
  padding: 4.6em 0;
}

@media (max-width: 991.98px) {
  .sponsers img {
    padding: 2em 0;
  }
}

@media (max-width: 767.98px) {
  .sponsers img {
    padding: 1.5rem 0;
  }
}

/*End Sponser*/
/*
Start User Interface*/
.user-interface {
  padding: 100px 0;
}

@media (max-width: 767.98px) {
  .user-interface {
    padding: 50px 0 25px 0;
  }
}

.user-interface h2 {
  max-width: 520px;
  margin-bottom: 1rem;
}

.user-interface p {
  margin-bottom: 2.5rem;
}

.user-interface .card {
  border: none;
  margin-bottom: 2rem;
  padding: 3.5rem 0;
}

@media (max-width: 767.98px) {
  .user-interface .card {
    padding: 1.5rem 0;
  }
}

.user-interface .card img {
  margin-bottom: 1.5rem;
}

.user-interface .card .card-body {
  padding: 0 2.5rem;
}

@media (max-width: 1199.98px) {
  .user-interface .card .card-body {
    padding: 0 1.5rem;
  }
}

@media (min-width: 992px) {
  .user-interface .card-up {
    position: absolute;
    right: 3.5rem;
    top: -50%;
  }
}

/*
End User Interface*/
/*
Start Freelancing Platform*/
.platform .upwork, .platform .gas, .platform .insight {
  max-width: 150px;
  margin: auto;
}

.platform {
  text-align: center;
}

.platform .fa-star {
  color: #6DDE54;
}

/*
End Freelancing Platform*/
/*
Start Client Comments*/
.client-comments {
  background-color: #23282E;
  padding: 3.5rem 0 3rem;
  color: #ffffff;
}

.client-comments img {
  float: right;
}

@media (max-width: 767.98px) {
  .client-comments img {
    float: none;
    max-width: 100%;
    margin: auto;
    display: block;
    margin-bottom: 1rem;
  }
}

.client-comments .p {
  font-size: 21px;
  font-weight: 700;
}

@media (max-width: 991.98px) {
  .client-comments .p {
    font-size: 18px;
  }
}

@media (max-width: 575.98px) {
  .client-comments .p {
    font-size: 16px;
  }
}

/*
End Client Comments*/
/*
Start Slider Secondary*/
.slider-secondary {
  overflow: hidden;
  padding: 15rem 0 4rem;
}

@media (max-width: 575.98px) {
  .slider-secondary {
    padding: 4rem 0 4rem;
  }
}

.slider-secondary .carousel-inner {
  overflow: initial;
}

.slider-secondary .carousel-caption {
  max-width: 60%;
  padding-top: 0;
  right: 0;
  bottom: 0;
  top: -25%;
  left: initial;
  z-index: 50;
  text-align: right;
  color: #1B1F23;
}

@media (max-width: 575.98px) {
  .slider-secondary .carousel-caption {
    max-width: 100%;
    position: initial;
    right: 15%;
    bottom: 20px;
    left: 15%;
    padding-top: 20px;
    padding-bottom: 20px;
    text-align: center;
  }
}

.slider-secondary .carousel-caption p {
  max-width: 400px;
  font-size: 21px;
  margin-bottom: 3.5rem;
}

@media (max-width: 991.98px) {
  .slider-secondary .carousel-caption p {
    max-width: 300px;
    font-size: 16px;
    margin-bottom: 1.5rem;
  }
}

@media (max-width: 575.98px) {
  .slider-secondary .carousel-caption p {
    float: none !important;
    text-align: center;
    max-width: 80%;
    margin: auto;
    margin-bottom: 1.5rem;
  }
}

.slider-secondary .carousel-indicators {
  z-index: 51;
}

@media (max-width: 767.98px) {
  .slider-secondary .carousel-indicators {
    bottom: -20px;
  }
}

@media (max-width: 575.98px) {
  .slider-secondary .carousel-indicators {
    bottom: -35px;
  }
}

.slider-secondary .carousel-indicators li {
  border: 1px solid transparent;
  background-color: #E4ECEE;
  opacity: 1;
  width: 1em;
  height: 1em;
  border-radius: 50%;
}

.slider-secondary .carousel-indicators li.active, .slider-secondary .carousel-indicators li:hover, .slider-secondary .carousel-indicators li:focus {
  background-color: #2FCCF6;
  border-color: #979797;
}

/*
End Slider Secondary*/
/*
Start Getting Touch*/
.getting-touch {
  padding: 7rem 0;
}

@media (max-width: 767.98px) {
  .getting-touch {
    padding: 3rem 0;
  }
}

.getting-touch h2 {
  margin-bottom: 1.75rem;
}

.getting-touch .div {
  margin-bottom: 3rem;
}

.getting-touch form {
  font-family: 'Roboto', sans-serif;
  border-radius: 10px;
  padding: 3rem 2.5rem 2.5rem 2.5rem;
}

.getting-touch form .btn {
  max-width: 100%;
  margin: auto;
  display: block;
}

/*
End Getting Touch*/
/*
Start Footer*/
.footer {
  position: relative;
  background-color: #23282E;
  color: #ffffff;
  padding: 6rem 0 4rem;
}

.footer .line {
  position: absolute;
  left: 0;
  right: 0;
  top: 30px;
  border: 1px solid #979797;
}

.footer a:hover {
  color: #2FCCF6;
}

.footer .span {
  margin-bottom: 7px;
  display: block;
}

.footer .list-unstyled li {
  padding: 5px;
}

.footer .list-unstyled li a {
  color: #ffffff;
}

.footer .list-unstyled li a:hover, .footer .list-unstyled li a:active {
  color: #2FCCF6;
}

.footer form .form-control {
  max-width: 280px;
}

.footer .social .fa {
  width: 2rem;
  text-align: center;
  margin-left: 10px;
  background-color: #2FCCF6;
  color: #000000;
  padding: 0.25em;
  font-size: 20px;
  margin-bottom: 10px;
}

.footer .social .fa:hover {
  color: #ffffff;
}

/*
End Footer*/
